import { Board, CardPlacement } from "../../core/Board";
import { Card } from "../../core/Card";
import { SpaceComponent } from "./SpaceComponent";
import { Component } from "./pixi/Component";

interface IPlayerInput {
  card: Card;
  rotation: Rotation;
  pointer: Coordinate;
  isSpecialAttack: boolean;
}

interface IBoardComponentProps {
  input: IPlayerInput;
}

type OnPlayerInputFn = (e: CardPlacement, ok: boolean) => void;

export class BoardComponent extends Component<IBoardComponentProps> {
  private readonly spaces: SpaceComponent[] = [];
  private overlay: SpaceComponent[] = [];

  private board: Board;
  private lock: boolean = false;

  private onPlayerInputFn: OnPlayerInputFn = () => {};

  layout = {
    width: 20,
    height: 27,
  };

  constructor() {
    super({
      input: null,
    });

    const spaceLayers = this.addLayers();
    for (let y = 0; y < this.layout.height; ++y) {
      for (let x = 0; x < this.layout.width; ++x) {
        const space = this.addComponent(new SpaceComponent(spaceLayers), {
          scale: { width: 1, height: 1 },
        });
        space.update({ interactive: true });
        space.boardPos = { x, y };
        this.spaces.push(space);
      }
    }

    this.onSpaceEvent("pointerover", (pointer) => {
      if (this.lock || this.props.input.value == null) {
        return;
      }
      this.update({
        input: Object.assign({}, this.props.input.value, {
          pointer,
        }),
      });
    });

    this.onSpaceEvent("pointertap", (pointer) => {
      if (this.props.input.value == null) {
        return;
      }
      const move = this.uiUpdateInput(
        Object.assign({}, this.props.input.value, {
          pointer,
        })
      );
      if (move == null) {
        return;
      }
      this.lock = true;
      Promise.all(this.overlay.map((s) => s.flashAnimation.play())).then(() => {
        this.lock = false;
        this.send("player.input", move);
      });
    });

    this.props.input.onUpdate((input) => this.uiUpdateInput(input));
  }

  private onSpaceEvent(
    event: string,
    callback: (pos: Coordinate, event: any) => void
  ) {
    for (const space of this.spaces) {
      const { boardPos } = space;
      space.hitbox.on(event, (event) => {
        callback(boardPos, event);
      });
    }
  }

  uiReset(board: Board) {
    this.board = board;
    this.spaces.forEach((e) => (e.visible = false));
    const { width, height } = board;
    const dx = (this.layout.width - board.width) / 2;
    const dy = (this.layout.height - board.height) / 2;
    for (let y = 0; y < height; ++y) {
      for (let x = 0; x < width; ++x) {
        const pos = { x, y };
        const space = this.get(pos);
        space.visible = true;
        space.position.set(x + dx, y + dy);
        space.update({
          value: board.get(pos),
        });
      }
    }
    this.uiUpdateFire();
  }

  uiUpdateFire() {
    this.board.forEach((x, y, v) => {
      const ok = this.board.isCharged({ x, y });
      this.get({ x, y }).update({ spFire: ok });
    });
  }

  uiPlaceCards(ms: CardPlacement[]): Promise<any> {
    console.assert(ms.length <= 2);
    if (ms.length == 2) {
      console.assert(ms[0].turn == -ms[1].turn);
    }
    this.board = this.board.move(ms);
    const spaces = new Set<SpaceComponent>();
    for (const m of ms) {
      m.card.rotate(m.rotation).forEach((x, y) => {
        const pos = { x: x + m.position.x, y: y + m.position.y };
        const space = this.get(pos);
        space.update({ value: this.board.get(pos) });
        spaces.add(space);
      });
    }
    const li: Promise<any>[] = [];
    spaces.forEach((s) => li.push(s.flashAnimation.play()));
    return Promise.all(li);
  }

  onPlayerInput(fn: OnPlayerInputFn) {
    this.onPlayerInputFn = fn;
  }

  get(pos: Coordinate) {
    console.assert(pos.x >= 0 && pos.x < this.board.width);
    console.assert(pos.y >= 0 && pos.y < this.board.height);
    return this.spaces[pos.x + pos.y * this.layout.width];
  }

  private uiResetOverlay() {
    for (const space of this.overlay) {
      space.update({
        overlay: 0,
      });
    }
    this.overlay = [];
  }

  private uiUpdateInput(input?: IPlayerInput): CardPlacement {
    this.uiResetOverlay();
    if (input == null) {
      return null;
    }

    const { card, rotation, pointer, isSpecialAttack } = input;
    if (
      card == null ||
      rotation == null ||
      pointer == null ||
      isSpecialAttack == null
    ) {
      return null;
    }

    const position = this.getAdjustedCardPosition(card, rotation, pointer);
    const move: CardPlacement = {
      turn: 1,
      card,
      rotation,
      position,
    };

    const ok = this.board.isValidMovement(isSpecialAttack, move);
    card.rotate(rotation).forEach((x, y, v) => {
      const pos = { x: x + position.x, y: y + position.y };
      const space = this.get(pos);
      space.update({
        overlay: v,
        overlayValid: ok,
      });
      this.overlay.push(space);
    });

    this.onPlayerInputFn(move, ok);

    return move;
  }

  private getAdjustedCardPosition(
    card: Card,
    rotation: Rotation,
    pointer: Coordinate
  ): Coordinate {
    let x = pointer.x - 3;
    let y = pointer.y - 3;

    if (rotation == 1 || rotation == 2) {
      x -= 1;
    }
    if (rotation == 2 || rotation == 3) {
      y -= 1;
    }

    const rotatedCard = card.rotate(rotation);
    const xs: number[] = [];
    const ys: number[] = [];
    rotatedCard.forEach((dx, dy, _) => {
      xs.push(x + dx);
      ys.push(y + dy);
    });

    const minX = Math.min(...xs);
    const maxX = Math.max(...xs);
    const minY = Math.min(...ys);
    const maxY = Math.max(...ys);
    if (minX < 0) {
      x += -minX;
    }
    if (minY < 0) {
      y += -minY;
    }
    if (maxX >= this.board.width) {
      x -= maxX - this.board.width + 1;
    }
    if (maxY >= this.board.height) {
      y -= maxY - this.board.height + 1;
    }

    return { x, y };
  }
}
