import { RenderTexture, Texture, WRAP_MODES } from "pixi.js";
import NrmOverlayGlsl from "../shaders/NrmOverlay.glsl";
import SpOverlayGlsl from "../shaders/SpOverlay.glsl";
import SpFireGlsl from "../shaders/SpFire.glsl";
import { Spaces } from "../../core/Common";
import { Component } from "./pixi/Component";
import { Layers } from "./pixi/Layers";
import { Color } from "./pixi/Color";
import { ColorPalette } from "./ColorPalette";
import { Animation } from "./pixi/Animation";
import { EaseFunc } from "./pixi/Ease";
import { Canvas } from "./pixi/Canvas";

const ID_TEXTURE_LOOKUP = new Map([
  [1, Texture.from("assets/player1_trivial_space.png")],
  [2, Texture.from("assets/player1_special_space.png")],
  [-1, Texture.from("assets/player2_trivial_space.png")],
  [-2, Texture.from("assets/player2_special_space.png")],
  [3, Texture.from("assets/neutral_space.png")],
]);

const ID_FLAT_TEXTURE_LOOKUP = new Map([
  [1, Texture.from("assets/pure_yellow.png")],
  [2, Texture.from("assets/pure_orange.png")],
]);

interface ISpFireComponentProps {
  primary: Color;
  secondary: Color;
  center: Color;
}

class SpFireComponent extends Component<ISpFireComponentProps> {
  public static readonly WIDTH = 104;

  layout = {
    width: SpFireComponent.WIDTH,
    height: SpFireComponent.WIDTH,
    center: {
      x: 52,
      y: 68,
      r: 15,
    },
  };

  constructor(props: ISpFireComponentProps) {
    super(props);

    const spFireShader = this.addShader(SpFireGlsl, {
      uColorPrimary: Color.BLACK.rgb01,
      uColorSecondary: Color.BLACK.rgb01,
      uAlphaSampler: Texture.from("assets/SpFire_00.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
      uFlameSampler: Texture.from("assets/SpFire_01.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
      uDistortionSampler: Texture.from("assets/SpFire_02.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
    });

    const {
      width,
      height,
      center: { x, y, r },
    } = this.layout;

    this.addSprite({
      width,
      height,
      filters: [spFireShader],
    });

    const circle = this.addSprite({
      anchor: 0.5,
      x,
      y,
      width: r * 2,
      height: r * 2,
      texture: Texture.from("assets/circle.png"),
    });

    this.props.primary.onUpdate(
      (v) => (spFireShader.uniforms.uColorPrimary = v.rgb01)
    );
    this.props.secondary.onUpdate(
      (v) => (spFireShader.uniforms.uColorSecondary = v.rgb01)
    );
    this.props.center.onUpdate((v) => (circle.tint = v.i32));
  }

  private static toTexture(props: ISpFireComponentProps): Texture {
    const sp = new SpFireComponent(props);
    const renderTexture = RenderTexture.create({
      width: sp.layout.width,
      height: sp.layout.height,
    });
    const renderer = (<Canvas>(<any>window).bgCanvas).app.renderer;
    const fn = () => {
      renderer.render(sp.ui, { renderTexture });
      window.requestAnimationFrame(fn);
    };
    window.requestAnimationFrame(fn);
    return renderTexture;
  }

  private static p1: Texture;
  public static get P1() {
    return this.p1 || (this.p1 = this.toTexture(ColorPalette.Player1.fire));
  }

  private static p2: Texture;
  public static get P2() {
    return this.p2 || (this.p2 = this.toTexture(ColorPalette.Player2.fire));
  }
}

interface ISpaceComponentProps {
  flat: boolean;
  value: number;
  spFire: boolean;
  overlayValid: boolean;
  overlay: number;
  bgAlpha: number;
  interactive: boolean;
}

export class SpaceComponent extends Component<ISpaceComponentProps> {
  layout = {
    width: 40,
    height: 40,
    border: {
      width: 1.5,
    },
    flash: {
      width: 67,
    },
  };

  public readonly hitbox;
  public boardPos?: Coordinate;

  public readonly flashAnimation: Animation;

  constructor(layers: Layers) {
    super({
      flat: false,
      value: Spaces.INVALID,
      spFire: false,
      overlayValid: false,
      overlay: 0,
      bgAlpha: 1,
      interactive: false,
    });

    const { width, border } = this.layout;

    this.hitbox = this.addSprite({
      width,
      height: width,
    });

    const nrmOverlayShader = this.addShader(NrmOverlayGlsl, {
      uColor: Color.BLACK.rgb01,
      uPatternSampler: Texture.from("assets/guide_stride.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
    });

    const spOverlayShader = this.addShader(SpOverlayGlsl, {
      uColor: Color.BLACK.rgb01,
      uDotSampler: Texture.from("assets/guide_dot.png", {
        wrapMode: WRAP_MODES.REPEAT,
      }),
    });

    let w = width - 2 * border.width;
    const bg = this.addGraphics({ layer: layers.get(0) })
      .beginFill(0x4b485b)
      .drawRect(0, 0, width, width)
      .beginFill(0x0a0a0a)
      .drawRect(border.width, border.width, w, w);

    const sprite = this.addSprite({
      width,
      height: width,
      layer: layers.get(1),
    });

    const spFire = this.addSprite({
      anchor: 0.5,
      x: width / 2,
      y: width / 2 - 16,
      layer: layers.get(2),
    });

    const overlay = this.addSprite({
      width,
      height: width,
      layer: layers.get(4),
    });

    w = this.layout.flash.width;
    const flashRoot = this.addContainer({
      x: width / 2,
      y: width / 2,
    });
    const flash = this.addSprite({
      parent: flashRoot,
      x: -w / 2,
      y: -w / 2,
      width: w,
      height: w,
      texture: Texture.from("assets/Decide.png"),
      layer: layers.get(5),
    });

    this.flashAnimation = this.addAnimation({
      time: 0.2,
      keyframe: (t) => {
        const ease = EaseFunc.EASE_IN_OUT_SINE;
        const alpha = 0.5;
        const scale = 1.5;
        flash.tint = ease.interpolate(
          ColorPalette.Space.flash.secondary,
          ColorPalette.Space.flash.primary,
          t
        ).i32;
        flash.alpha = alpha * ease.apply(1 - Math.abs(2 * t - 1));
        flashRoot.scale.set(EaseFunc.LINEAR.interpolate(1, scale, t));
      },
    });

    const fn = () => {
      const v = this.props.value.value;
      const flat = this.props.flat.value;
      bg.visible = Math.abs(v) != Spaces.INVALID;
      const map = !flat ? ID_TEXTURE_LOOKUP : ID_FLAT_TEXTURE_LOOKUP;
      sprite.texture = map.get(v);
      spFire.texture = v > 0 ? SpFireComponent.P1 : SpFireComponent.P2;
    };
    this.props.value.onUpdate(fn);
    this.props.flat.onUpdate(fn);

    this.props.spFire.onUpdate((ok) => (spFire.visible = ok));

    this.props.overlayValid.onUpdate((ok) => {
      nrmOverlayShader.uniforms.uColor = ok
        ? ColorPalette.Space.overlay.normal.rgb01
        : ColorPalette.Space.overlay.invalid.rgb01;
      spOverlayShader.uniforms.uColor = ok
        ? ColorPalette.Space.overlay.special.rgb01
        : ColorPalette.Space.overlay.invalid.rgb01;
    });

    this.props.overlay.onUpdate((v) => {
      overlay.visible = true;
      if (v == Spaces.TRIVIAL) {
        overlay.filters = [nrmOverlayShader];
      } else if (v == Spaces.SPECIAL) {
        overlay.filters = [spOverlayShader];
      } else {
        overlay.visible = false;
      }
    });

    this.props.bgAlpha.onUpdate((alpha) => {
      bg.alpha = alpha;
    });

    this.props.interactive.onUpdate((ok) => {
      this.hitbox.interactive = ok;
    });
  }
}
