import { Card } from "../../core/Card";
import { SpaceComponent } from "./SpaceComponent";
import { Component } from "./pixi/Component";
import { Spaces } from "../../core/Common";

interface ICardGridComponentProps {
  card: Card;
  turn: Turn;
  flat: boolean;
  bgAlpha: number;
}

export class CardGridComponent extends Component<ICardGridComponentProps> {
  private readonly spaces: SpaceComponent[] = [];

  layout = {
    width: 8,
    height: 8,
  };

  constructor() {
    super({
      card: Card.fromId(1),
      turn: 1,
      flat: false,
      bgAlpha: 0.75,
    });

    const spaceLayers = this.addLayers();
    for (let y = 0; y < 8; ++y) {
      for (let x = 0; x < 8; ++x) {
        const space = this.addComponent(new SpaceComponent(spaceLayers), {
          scale: { width: 1, height: 1 },
          x,
          y,
        });
        space.update({ flat: true });
        space.boardPos = { x, y };
        this.spaces.push(space);
      }
    }

    const fn = () => {
      const card = this.props.card.value;
      const turn = this.props.turn.value;
      const bgAlpha = this.props.bgAlpha.value;
      for (const space of this.spaces) {
        const v = card.get(space.boardPos);
        space.update({
          value: turn * v,
          bgAlpha: v == Spaces.EMPTY ? bgAlpha : 1,
        });
      }
    };
    this.props.card.onUpdate(fn);
    this.props.turn.onUpdate(fn);
    this.props.bgAlpha.onUpdate(fn);

    this.props.flat.onUpdate((flat) =>
      this.spaces.forEach((s) => s.update({ flat }))
    );
  }

  get(pos: Coordinate) {
    console.assert(pos.x >= 0 && pos.x < 8);
    console.assert(pos.y >= 0 && pos.y < 8);
    return this.spaces[pos.x + pos.y * 8];
  }
}
